      width: 100%;
      max-height: 25rem;
      object-fit: contain; } }
  @media (min-width: 600px) {
    .articles--old .article__wrapper {
      display: table; } }
  @media (min-width: 600px) {
    .articles--old .article {
      display: table;
      min-height: 25rem; }
      .articles--old .article picture {
        display: table-cell;
