.slider__container {
  position: relative; }

.slider__content {
  position: relative;
  z-index: 1;
  list-style: none;
  overflow: hidden;
  margin-bottom: 20px; }
  @media (min-width: 700px) {
    .slider__content {
      margin: 0 -32px;
      padding: 32px 32px 200px; } }

.slider__swiper {
  position: relative;
  z-index: 1;
  display: flex;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  transition-property: transform; }

.slider__article {
  position: relative;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  transition-property: transform; }
  .slider__article img {
    display: block;
    margin: 0 auto; }

.slider__controls {
  content: "";
  display: none;
  position: absolute;
  left: 0;
  top: 45%;
  transform: translateY(-50%);
  width: 100%; }
  .slider__controls button {
    margin: 0;
    outline: none;
    border: none;
    padding: 0;
    background: none;
    cursor: pointer;
    position: absolute;
    left: -50px;
    top: 50%;
    z-index: 2;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
    background-color: #fff;
    transform: translateY(-50%); }
    .slider__controls button:focus {
      outline: none; }
    .slider__controls button svg {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) rotate(90deg); }
    .slider__controls button:last-of-type {
      left: unset;
      right: -50px;
      transform: translateY(-50%); }
      .slider__controls button:last-of-type svg {
        transform: translate(-50%, -50%) rotate(-90deg); }
  @media (min-width: 1311px) {
    .slider__controls {
      display: block; } }

.slider__text {
  right: 20px;
  bottom: -200px;
  z-index: 3;
  flex: 1 1 auto;
  flex-direction: column;
  align-items: flex-start;
  border-radius: 1rem;
  padding: 15px;
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  opacity: 0;
  transition: 0.8s ease-in-out; }
  .swiper-slide-active .slider__text {
    bottom: -50px;
    opacity: 1; }
  @media (min-width: 700px) {
    .slider__text {
      position: absolute;
      width: 40%; } }

.swiper-fade .swiper-slide {
  transition-property: opacity;
  pointer-events: none; }
  .swiper-fade .swiper-slide .swiper-slide {
    pointer-events: none; }

.swiper-fade .swiper-slide-active,
.swiper-fade .swiper-slide-active .swiper-slide-active {
  pointer-events: auto; }

.swiper-fade.swiper-free-mode .swiper-slide {
  transition-timing-function: ease-out; }
